@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Ubuntu);
header{
   
    padding: 20px;
    background-color:#C04626;
    color: #73453A;
    border-bottom: 10px solid #F5937A;
  }
  
  nav {
    min-width: 13em;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    text-align: right;
  }
  
  nav p {
    margin: 0 20px 0 0;
    max-width: 10em;
    overflow: hidden;
  }
  
  nav span { padding: 0 .75em }
  
  .flex{
    display: flex;
  }
  
  .header-content {
    justify-content: space-between;
    align-items: center;
    max-width: 65em;
    margin: 0 auto;
  }
  
  .header-content > * { flex: 3 1 }
  
  .navLink{
    padding: 0 .5em;
    font-size: 20px;
    text-decoration: none;
    color: #1c2a43;
  }
  
  .navLogout {
    border: 3px solid #F5937A;
    padding: .5em 1em;
    border-radius: 5px;
  }
  
  .headerTitle{
    font-size: .75em;
    max-width: 7em;
  }

  
  @media only screen and (max-width: 425px) {
    nav span,
    #logo {
      display: none;
    }
  
    .headerTitle {
      font-size: 5vw;
      max-width: 5em;
    }
  }
.stats {
    width: 100vw;
    border-top: 3px groove white;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    max-height: 10vh;
  }
  
  .stats p{
    font-size: 0.7em;
    margin: 3px;
  }
  
  button{
    margin-bottom: 3px;
  }
  
  .stats strong {
    font-size: 1.1em;
  }


.textButton{
    background-color: #dce3e9;
    color: #732A17;
    border: 1px solid #0A0908; 
    border-radius: 3px;
    padding: 6px 12px 6px 12px;
    cursor: pointer;
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
  }
  
  .right{
    justify-content: flex-end;
  }
  
  .center{
    justify-content: center;
  }
  
  .btn-register {
    margin: 20px 30px 0 0;
  }
  
  .alreadyAccountLink{
    margin-top: 1.75em;
    font-size: 0.9em;
    color: #732A17 !important;
    text-align: right;
  }
  
  
  .tagline {
    display: flex;
    flex: 1 1;
    align-items: center;
    font-size: 1.5em;
    text-align: left;
    padding: 0 2em;
    flex-basis: 7em;
    color: #fff;
  }
  
  .registration-container .formContainer {
    flex: 2 1;
    flex-basis: 25em;
  }
  
  
  @media only screen and (max-width: 760px) {
    .tagline { text-align: center }
  }


.dashboardContainer button {
    margin-bottom: 20px;
  }
  
  .dashboardContainer h2 {
    text-align: center;
  }
  
  .infoArea {
    border: 3px solid white;
    border-radius: 3px;
    background: rgba(255,255,255,0.4);
    min-height: 50%;
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }
  
  .infoHeader {
    height: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .infoHeader h3{
    text-align: center;
    height: 100%;
  }
  
  .wordsHolder {
    border-top: 3px solid white;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    overflow: scroll;
  }
  
  .words {
    min-width: 100%;
    max-width: 100%;
  }
  
  .words h4 {
    text-align: center;
    margin-top: 0px;
    margin-bottom: 8px;
  }
  
  .words ul {
    margin: 0px;
    padding: 0px;
  }
  .words li {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 5px;
    border: 1px solid white;
    border-radius: 3px;
    margin-bottom: 5px;
  }
  
  button {
    margin-bottom: 20px;
  }
  
  li {
    text-decoration: none;
    list-style: none;
  }
.correct {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    border-top: 3px solid white;
    height: 100%;
  }
  
  .correct p{
    text-align: center;
  }
  .correct button {
    margin-top: 10px;
  }
.incorrect {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    border-top: 3px solid white;
    height: 100%;
  }
  
  .incorrect p{
    text-align: center;
  }
  .incorrect button {
    margin-top: 10px;
  }
.guess {
    width: 100%;
    max-height: 60%;
    display: flex;
  }
  .guess label {
    margin-bottom: 10px;
  }
  
  
  form {
    font-size: 1.2em;
    background-color: rgba(255,255,255,0.2);
    display: flex;
  }
  
  form button {
    margin-top: 15px;
  }
  .stack {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .guess input {
    height: 20vh;
    width: 100%;
    background-color: rgba(255,255,255,0.8);
    text-align: center;
    font-size: 3em;
  }
  
  input:focus{
    background-color: rgba(255,255,255, 0.4);
    text-shadow: 2px 2px rgba(0,0,0,0.2);
    box-shadow: 0 4px 8px 0 rgba(255,255,255,0.7), 0 6px 20px 0 rgba(255,255,255,0.8);
  }
  
  .infoHeader span{
    font-size: 2.5em;
    font-style: oblique;
  }

body {
  margin: 0;
  padding: 0;
  background-color: #F35831;
  font-family: 'Roboto';
}



main {
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

h1 {
  margin: 0;
  line-height: 0.75em;
}

h2 {
  margin: 1em 0 0.25em;
  font-size: 2.5em;
  color: #f7f7f7;
}

h3 {
  margin: 3em auto 1em;
  padding: 0 0 0.75em;
  max-width: 80%;
  border-bottom: 1px solid #5366c5;
  color: #5366c5;
  font-family: 'Roboto';
  font-feature-settings: "smcp", "c2sc";
  font-variant: all-small-caps;
  font-size: 2em;
}

a:link,
a:visited {
  color: inherit;
  cursor: pointer;
}


.login-container,
.registration-container {
  margin: 5% 0 0;
  padding: 3em;
  display: flex;
  flex-wrap: wrap;
}

.formContainer {
  display: flex;
  justify-content: center;
  flex-direction: column;
}



.formField {
  margin: 1.2em 0 0 0;
  padding: 0;
  text-align: left;
  display: flex;
  margin-bottom: 5px;
  width: 100%;
}

.inputLabel {
  width: 10em;
  margin: 0;
  padding: 7px 15px;
  border-radius: 4px 0 0 4px;
  color: #F35831;
  background-color: #f7f7f7;
  font-size: 0.9em;
  line-height: 1.9em;
}

.inputField {
  width: 100%;
  margin: 0;
  padding: 7px 12px;
  border: 3px solid transparent;
  border-radius: 0 5px 5px 0;
  border-left: 0;
  border-bottom-color: #f7f7f7;
  font-size: 1.2em;
  font-weight: bold;
  background: transparent;
  color: #f7f7f7;
  transition: all .25s;
}

.inputField:focus {
  border-color: #f7f7f7;
  background-color: #519c9c;
  outline: none;
}

.inputForm footer {
  display: flex;
  flex-direction: column;
}

.btn {
  margin: 1em 0 0;
  padding: 10px 20px;
  background-color: #ffc64d;
  color: #4a4a4a;
  border: 0;
  border-radius: 3px;
  cursor: pointer;
  font-size: 1.2em;
  font-weight: bold;
  box-shadow: 5px 6px 0px 1px goldenrod;
  transition: all .4s;
}

.btn:hover {
  background-color: #fff5d9;
  box-shadow: 10px 11px 0px 1px #deb41f;
}


.hidden {
  display: none !important;
  opacity: 0;
}



@media only screen and (max-width: 425px) {
  h2 {
    margin: 1em 0 0.25em;
    font-size: 15vw;
  }
}
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Ubuntu", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root {
  --font-size: calc(10px + 1vmin);
}

