.textButton{
    background-color: #dce3e9;
    color: #732A17;
    border: 1px solid #0A0908; 
    border-radius: 3px;
    padding: 6px 12px 6px 12px;
    cursor: pointer;
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
  }
  
  .right{
    justify-content: flex-end;
  }
  
  .center{
    justify-content: center;
  }
  
  .btn-register {
    margin: 20px 30px 0 0;
  }
  
  .alreadyAccountLink{
    margin-top: 1.75em;
    font-size: 0.9em;
    color: #732A17 !important;
    text-align: right;
  }
  
  
  .tagline {
    display: flex;
    flex: 1;
    align-items: center;
    font-size: 1.5em;
    text-align: left;
    padding: 0 2em;
    flex-basis: 7em;
    color: #fff;
  }
  
  .registration-container .formContainer {
    flex: 2;
    flex-basis: 25em;
  }
  
  
  @media only screen and (max-width: 760px) {
    .tagline { text-align: center }
  }